<template>
    <div id="content">
        <div class="container">
            <div class="feature-title">{{title}}</div>

            <div id="items">
                <select class="form-control" v-model="coursePlan" v-if="!loading || coursePlans.length != 0" v-on:change="loadCourses">
                    <option disabled value="">Pilih jenis mata kuliah/peminatan...</option>
                    <option v-for="plan in coursePlans" :value="plan.id">{{ plan.name }}</option>
                </select>
                <select v-if="coursePlan != ''" class="form-control" v-model="course" v-on:change="loadMaterials" style="margin-top: 5px;">
                    <option disabled value="">Pilih mata kuliah...</option>
                    <option v-for="c in courses" :value="c.id">{{ c.name }}</option>
                </select>
                <div v-if="loadingItems || loading" class="text-center" style="margin-top: 20px; color: white;"><img src="../assets/img/loading.gif" height="30" alt="loading..." /><br />Memuat data...</div>
                <div v-if="empty && showEmptyMessage && !loadingItems && !loading" class="col-12 text-center empty" style="margin-top: 20px;">
                    Saat ini masih belum ada {{ title }}.
                </div>
                <div v-if="!empty && showMaterials && !loadingItems && !loading" class="materials">
                    <span v-for="material in materials"><a :href="api + '/..' + material.file" target="_blank">{{ material.title }}</a><br /></span>
                </div>
            </div>
        </div>
    </div>
</template>

<script>

export default {
    name: 'courses-centered-feature',
    data: function() {
        return {
            coursePlan: '',
            course: '',
            coursePlans: [],
            courses: [],
            materials: [],
            loading: true,
            loadingItems: false,
            showEmptyMessage: false,
            showMaterials: false
        }
    },
    methods: {
        loadCourses() {
            this.course = '';
            this.$http.get('http://fhuiguide.com/api/courses?c=' + this.coursePlan).then(function(response){
                var data = response.json();
                if (data.status == 'Success') {
                    this.courses = data.data;
                }
                this.showEmptyMessage = false;
                this.showMaterials = false;
            });
        },
        loadMaterials() {
            this.loadingItems = true;
            this.materials = [];
            this.$http.get(this.api + '?c=' + this.course).then(function(response){
                var data = response.json();
                if (data.status == 'Success') {
                    this.materials = data.data;
                }
                this.loadingItems = false;
                this.showEmptyMessage = true;
                this.showMaterials = true;

            });
        }
    },
    props: {
        title: {
            default: ''
        },
        api: {
            default: 'http://fhuiguide.com/api'
        }
    },
    created: function() {

        // Load course plans
        this.$http.get('http://fhuiguide.com/api/course_plans').then(function(response){
            var data = response.json();
            if (data.status == 'Success') {
                this.coursePlans = data.data;

                this.loading = false;
            }
        });
    },
    computed: {
        empty: function() {
            return this.materials.length < 1;
        }
    }
}

</script>

<style scoped>

.feature-title {
    width: 100%;
    margin-top: 20px;
    font-size: 28px;
    font-family: 'Dosis', sans-serif;
    font-weight: bold;
    color: #FFFFFF;
    border-bottom: 1px solid #FFFFFF;
    margin-bottom: 10px;
}

#content {
    margin: 0px;
    background-color: #2D4452;
    padding: 1px 50px 50px 50px;
}

#items .empty {
    color: #FFFFFF;
}

.materials {
    padding: 20px;
    background-color: #FFFFFF;
    margin-top: 10px;
}

@media screen and (max-width: 576px) {

    #content {
        padding: 1px 0px 20px 0px;
    }

}
</style>



