<template>
    <div class="wrapper">
        <div class="container">
            <div class="login col-12 col-md-6">
                <div class="row">
                    <div class="title col-12">
                        <h2>Admin Area</h2>
                    </div>
                </div>
                <div class="row">
                    <div class="form col-12">
                        <div class="form-group">
                            <label for="username">User:</label>
                            <input type="text" class="form-control" id="username" placeholder="User" v-model="username">
                        </div>
                        <div class="form-group">
                            <label for="username">Password:</label>
                            <input type="password" class="form-control" id="username" placeholder="Password" v-model="password">
                        </div>
                        <button type="submit" class="btn btn-default btn-block" v-on:click="login($event)">Login</button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
var md5 = require('md5')

export default {
    name: "login",
    data: function(){
        return {
            username: '',
            password: ''
        }
    },
    methods: {
        login: function(e) {
            e.preventDefault();

            this.$http.post('http://fhuiguide.com/api/login', {username: this.username, password: this.password})
            .then(function(response){
                var data = response.json();

                if (data.status == 'Success') {
                    console.log(data.data[0]['auth-token']);
                    localStorage.setItem('auth-token', data.data[0]['auth-token']);
                    this.$router.push({name: 'Dashboard'});
                }
            });
        },
        logout: function() {
            this.token = '';
            localStorage.removeItem('auth-token');
        }
    },
    beforeCreate: function() {
        var token = localStorage.getItem('auth-token');
        if (token) {
            this.$http.post('http://fhuiguide.com/api/check-token', {authToken: token})
            .then(function(response){
                var data = response.json();

                if (data.status == 'Success') {
                    this.$router.push({name: 'Dashboard'});
                }
            });
        }
    }
}
</script>

<style scoped>

.wrapper {
    background-color: #60170D;
    min-height: 450px;
    padding-top: 50px;
    padding-bottom: 50px;
}

.login {
    margin: auto;
    background-color: #B4311E;
    height: 300px;
    padding-top: 10px;
    padding-bottom: 10px;
    color: #FFFFFF;
}

.login .title {
    border-bottom: 1px solid #FFFFFF;
}

.login .title h2 {
    font-family: 'Dosis', sans-serif;
    font-weight: bold;
}

.form {
    padding-top: 10px;
}

</style>



