<template>
    <div class="controls col-12 col-md-10">
        <div class="title">
            <h3>Dashboard Info Tugas</h3>
        </div>

        <div class="row">
            <div class="control col-12">
                <table class="table table-bordered table-responsive">
                    <thead>
                        <tr>
                            <th>Nama Tugas</th>
                            <th>Mata Kuliah</th>
                            <th>Deadline</th>
                            <th>Pengaturan</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr v-if="!loading" v-for="homework in homeworks">
                            <td>{{ homework.name }}</td>
                            <td>{{ homework.course }}</td>
                            <td>{{ readableDate(homework.deadline) }}</td>
                            <td>
                                <button class="btn btn-sm btn-danger" data-toggle="modal" data-target="#deleteModal" v-on:click="select(homework.id)" v-if="!deleting || selectedPost != homework.id">Hapus</button>
                                <img v-else-if="deleting && selectedPost == homework.id" src="../assets/img/loading.gif" height="30" alt="loading..." />
                            </td>
                        </tr>
                        <tr v-if="loading">
                            <td colspan="4" class="text-center">
                                <img src="../assets/img/loading.gif" height="30" alt="loading..." /><br />Memuat data...
                            </td>
                        </tr>
                        <tr v-if="tooMany">
                            <td colspan="4" class="text-center">
                                <button class="btn btn-sm btn-block btn-secondary" v-on:click="loadMore">Lihat lebih banyak</button>
                            </td>
                        </tr>
                        <tr v-if="empty">
                            <td colspan="4" class="text-center">
                                Masih belum ada info tugas.
                            </td>
                        </tr>
                    </tbody>
                </table>
            </div>

            <div class="control col-12">
                <div class="name">Tambah Tugas Baru</div>
                <form>
                    <div v-if="!sending">
                        <div class="form-group">
                            <input type="text" class="form-control input-sm" placeholder="Nama Tugas" v-model="new_name" required>
                        </div>
                        <div class="form-group">
                            <input id="datetimepicker" type="text" class="form-control input-sm" placeholder="Deadline" required />
                        </div>
                        <div class="form-group">
                            <input type="text" class="form-control input-sm" placeholder="Mata Kuliah" v-model="new_course" required>
                        </div>
                        <button type="submit" class="btn btn-secondary btn-block" v-on:click="submit">Tambah</button>
                    </div>
                    <div class="col-12 text-center" v-else><img src="../assets/img/loading.gif" height="30" alt="loading..." /><br />Mengirim data...</div>
                </form>
            </div>
        </div>

        <MatkulManager api="http://fhuiguide.com/api/courses" />

        <div class="modal fade" id="deleteModal" tabindex="-1" role="dialog" aria-labelledby="deleteModal" aria-hidden="true">
            <div class="modal-dialog" role="document">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title">Hapus Post</h5>
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close" v-on:click="unselect">
                            <span aria-hidden="true">&times;</span>
                        </button>
                    </div>
                    <div class="modal-body">
                        <p>Apakah kamu yakin akan menghapus info tugas tersebut?</p>
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-danger" data-dismiss="modal" v-on:click="deleteSelected">Hapus</button>
                        <button type="button" class="btn btn-secondary" data-dismiss="modal" v-on:click="unselect">Batal</button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import MatkulManager from './MatkulManager'

    export default {
        name: 'info-tugas-dashboard',
        mounted: function() {
            this.$emit('page', 'tugas');
    	    $('#datetimepicker').datetimepicker();
        },
        data: function() {
            return {
                api: 'http://fhuiguide.com/api/homeworks',
                homeworks: [],
                tooMany: false,
                page: 1,
                loading: true,
                new_name: '',
                new_course: '',
                sending: false,

                selectedPost: null,
                deleting: false,
            }
        },
        methods: {
            submit: function() {
                this.sending = true;

                var data = {
                    name: this.new_name,
                    course: this.new_course,
                    deadline: $('#datetimepicker').val()
                }

                var headers = {
                    headers: {
                        Authorization: localStorage.getItem('auth-token')
                    }
                }

                this.$http.post(this.api, data, headers)
                .then(function(response){
                    var data = response.json();

                    if (data.status == 'Success') {

                        this.loadHomeworks();

                    } else if (data.status == 'Error') {
                        alert('Terjadi kesalahan! ' + data.message);
                    } else {
                        alert('Terjadi kesalahan!');
                    }

                    this.new_name = '';
                    this.new_course = '';
                    $('#datetimepicker').val('');
                    $('#datetimepicker').datetimepicker();
                    this.sending = false;
                })
                .catch(function(response){
                    alert('Terjadi kesalahan!');
                    this.sending = false;
                });
            },
            loadHomeworks: function() {
                this.loading = true;

                this.$http.get(this.api)
                .then(function(response){
                    var data = response.json();

                    if (data.status == 'Success') {

                        this.homeworks = data.data;
                        this.tooMany = data.more;

                    } else if (data.status == 'Error') {
                        alert('Terjadi kesalahan! ' + data.message);
                    } else {
                        alert('Terjadi kesalahan!');
                    }

                    this.page = 1;
                    this.loading = false;
                })
                .catch(function(response){
                    alert('Terjadi kesalahan!');
                    this.loading = false;
                });

            },
            loadMore: function() {
                this.page++;
                this.loading = true;

                this.$http.get(this.api + '?p=' + this.page)
                .then(function(response){
                    var data = response.json();

                    if (data.status == 'Success') {

                        this.homeworks = this.homeworks.concat(data.data);
                        this.tooMany = data.more;

                    } else if (data.status == 'Error') {
                        alert('Terjadi kesalahan! ' + data.message);
                    } else {
                        alert('Terjadi kesalahan!');
                    }

                    this.loading = false;
                })
                .catch(function(response){
                    alert('Terjadi kesalahan!');
                });

            },
            readableDate: function(date) {
                var dateTime = date.split(' ');

                var date = dateTime[0];
                var time = dateTime[1];

                var dateComponent = date.split('-');
                var year = dateComponent[0];
                var month = parseInt(dateComponent[1]);
                var day = parseInt(dateComponent[2]);

                var timeComponent = time.split(':');
                var hour = timeComponent[0];
                var minute = timeComponent[1];

                var months = ['', 'Januari', 'Februari', 'Maret', 'April', 'Mei', 'Juni', 'Juli', 'Agustus', 'September', 'Oktober', 'November', 'Desember'];

                var date = day + ' ' + months[month] + ' ' + year + ' ' + hour + ':' + minute;

                return date;
            },
            select: function(id) {
                this.selectedPost = id;
            },
            deleteSelected: function() {
                this.deleting = true;
                this.$http.delete(this.api + '/' + this.selectedPost, {headers: {Authorization: localStorage.getItem('auth-token')}})
                .then(function(response) {
                    var data = response.json();

                    if (data.status == 'Success') {
                        this.loadHomeworks();
                    }

                    this.deleting = false;
                });
            },
            unselect: function() {
                this.selectedPost = null;
            }
        },
        created: function() {
            this.loadHomeworks();
        },
        computed: {
            empty: function() {
                return !this.loading && this.homeworks.length < 1;
            }
        },
        components: {
            MatkulManager
        }
    }

</script>

<style scoped>

.controls {
    background-color: #F4F4F4;
    padding: 20px;
}

.title h3 {
    font-family: 'Dosis', sans-serif;
    font-weight: bold;
}

.control {
    margin-top: 10px;
    padding: 10px;
    background-color: #FFFFFF;
}

</style>



