<template>
    <div id="content">
        <div class="container">
            <div class="feature-title">Info Tugas</div>

            <div id="items">
                <table class="table table-bordered table-responsive">
                    <thead>
                        <tr>
                            <th>Nama Tugas</th>
                            <th>Mata Kuliah</th>
                            <th>Deadline</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr v-if="!loading" v-for="homework in homeworks"  v-bind:class="{ isLate: late(homework.deadline) }">
                            <td>{{ homework.name }}</td>
                            <td>{{ homework.course }}</td>
                            <td>{{ readableDate(homework.deadline) }}</td>
                        </tr>
                        <tr v-if="loading">
                            <td colspan="4" class="text-center">
                                <img src="../assets/img/loading.gif" height="30" alt="loading..." /><br />Memuat data...
                            </td>
                        </tr>
                        <tr v-if="tooMany">
                            <td colspan="4" class="text-center">
                                <button class="btn btn-sm btn-block btn-secondary" v-on:click="loadMore">Lihat lebih banyak</button>
                            </td>
                        </tr>
                        <tr v-if="empty">
                            <td colspan="4" class="text-center">
                                Masih belum ada info tugas.
                            </td>
                        </tr>
                    </tbody>
                </table>
                <span style="color: white; font-size: 12px;">*) Yang berwarna abu-abu berarti sudah melewati deadline.</span>
            </div>
        </div>
    </div>
</template>

<script>

export default {
    name: 'info-tugas',
    data: function() {
        return {
            api: 'http://fhuiguide.com/api/homeworks',
            homeworks: [],
            tooMany: false,
            page: 1,
            loading: true
        }
    },
    methods: {
        loadMore: function() {
            this.page++;
            this.loading = true;

            this.$http.get(this.api + '?p=' + this.page)
            .then(function(response){
                var data = response.json();

                if (data.status == 'Success') {

                    this.homeworks = this.homeworks.concat(data.data);
                    this.tooMany = data.more;

                } else if (data.status == 'Error') {
                    alert('Terjadi kesalahan! ' + data.message);
                } else {
                    alert('Terjadi kesalahan!');
                }

                this.loading = false;
            })
            .catch(function(response){
                alert('Terjadi kesalahan!');
            });

        },
        readableDate: function(date) {
            var dateTime = date.split(' ');

            var date = dateTime[0];
            var time = dateTime[1];

            var dateComponent = date.split('-');
            var year = dateComponent[0];
            var month = parseInt(dateComponent[1]);
            var day = parseInt(dateComponent[2]);

            var timeComponent = time.split(':');
            var hour = timeComponent[0];
            var minute = timeComponent[1];

            var months = ['', 'Januari', 'Februari', 'Maret', 'April', 'Mei', 'Juni', 'Juli', 'Agustus', 'September', 'Oktober', 'November', 'Desember'];

            var date = day + ' ' + months[month] + ' ' + year + ' ' + hour + ':' + minute;

            return date;
        },
        late: function(deadline) {
            var now = new Date();
            var deadline = new Date(deadline.replace(' ', 'T'));

            return (deadline - now) < 1;
        }
    },
    created: function() {
        this.$http.get(this.api)
        .then(function(response){
            var data = response.json();

            if (data.status == 'Success') {

                this.homeworks = data.data;
                this.tooMany = data.more;

            } else if (data.status == 'Error') {
                alert('Terjadi kesalahan! ' + data.message);
            } else {
                alert('Terjadi kesalahan!');
            }

            this.page = 1;
            this.loading = false;
        })
        .catch(function(response){
            alert('Terjadi kesalahan!');
            this.loading = false;
        });
    },
    computed: {
        empty: function() {
            return !this.loading && this.homeworks.length < 1;
        }
    }
}

</script>

<style scoped>

.feature-title {
    width: 100%;
    margin-top: 20px;
    font-size: 28px;
    font-family: 'Dosis', sans-serif;
    font-weight: bold;
    color: #FFFFFF;
    border-bottom: 1px solid #FFFFFF;
    margin-bottom: 10px;
}

#content {
    margin: 0px;
    background-color: #2D4452;
    padding: 1px 50px 50px 50px;
}

table {
    background-color: #FFFFFF;
}

#items .empty {
    color: #FFFFFF;
}

.isLate>td {
    background-color: #AAAAAA;
    color: #EEEEEE;
    font-style: italic;
}

.materials {
    padding: 20px;
    background-color: #FFFFFF;
    margin-top: 10px;
}

@media screen and (max-width: 576px) {

    #content {
        padding: 1px 0px 20px 0px;
    }

}
</style>



