<template>
    <div class="wrapper row" v-if="authorized">
        <div class="nav col-12 col-md-2">
            <div class="col-12 nav-links">
                <ul>
                    <li class="header">Pengembangan Karir</li>
                    <router-link :to="{ name: 'Dashboard Info Lowker'}"><li v-bind:class="{ active: nowActive == 'lowker' }"><img src="../assets/img/jobs.png" alt="logo"> Info Lowongan Kerja</li></router-link>
                    <router-link :to="{ name: 'Dashboard Pengkarpedia'}"><li v-bind:class="{ active: nowActive == 'pengkarpedia' }"><img src="../assets/img/judge.png" alt="logo"> Pengkarpedia</li></router-link>
                    <router-link :to="{ name: 'Dashboard Info Magang'}"><li v-bind:class="{ active: nowActive == 'magang' }"><img src="../assets/img/job-search.png" alt="logo"> Info Magang</li></router-link>
                    <router-link :to="{ name: 'Dashboard Company Profile'}"><li v-bind:class="{ active: nowActive == 'company' }"><img src="../assets/img/building.png" alt="logo"> Company Profile</li></router-link>

                    <li class="header">Pendidikan &amp; Keilmuan</li>
                    <router-link :to="{ name: 'Dashboard Rancangan Kuliah'}"><li v-bind:class="{ active: nowActive == 'rancangan-kuliah' }"><img src="../assets/img/bookmark.png" alt="logo"> Rancangan Kuliah</li></router-link>
                    <router-link :to="{ name: 'Dashboard Info Tugas'}"><li v-bind:class="{ active: nowActive == 'tugas' }"><img src="../assets/img/notepad.png" alt="logo"> Info Tugas</li></router-link>
                    <router-link :to="{ name: 'Dashboard Bahan Kuliah'}"><li v-bind:class="{ active: nowActive == 'bahan-kuliah' }"><img src="../assets/img/books.png" alt="logo"> Bahan Kuliah</li></router-link>
                    <router-link :to="{ name: 'Dashboard Fasilitator Lomba'}"><li v-bind:class="{ active: nowActive == 'lomba' }"><img src="../assets/img/diploma.png" alt="logo"> Fasilitator Lomba</li></router-link>
                    <router-link :to="{ name: 'Dashboard PKM Guide'}"><li v-bind:class="{ active: nowActive == 'pkm' }"><img src="../assets/img/quill.png" alt="logo"> PKM Guide</li></router-link>
                    <router-link :to="{ name: 'Dashboard Bank Soal'}"><li v-bind:class="{ active: nowActive == 'soal' }"><img src="../assets/img/clipboard.png" alt="logo"> Bank Soal</li></router-link>

                    <li class="header">Advokasi</li>
                  <router-link :to="{ name: 'Dashboard Advografis'}"><li v-bind:class="{ active: nowActive == 'advografis' }"><img src="../assets/img/formula.png" alt="logo"> Advografis</li></router-link>
                  <router-link :to="{ name: 'Dashboard Info BOP'}"><li v-bind:class="{ active: nowActive == 'bop' }"><img src="../assets/img/graduation.png" alt="logo"> Info BOP</li></router-link>
                  <router-link :to="{ name: 'Dashboard Lapor Advo'}"><li v-bind:class="{ active: nowActive == 'lapor' }"><img src="../assets/img/telemarketer.png" alt="logo"> Lapor Advo</li></router-link>
                  <router-link :to="{ name: 'Dashboard Info Beasiswa'}"><li v-bind:class="{ active: nowActive == 'beasiswa' }"><img src="../assets/img/scholarship.png" alt="logo"> Info Beasiswa</li></router-link>
                </ul>
                <div class="logout col-12">
                    <button v-on:click="logout" class="btn btn-sm btn-block">Logout</button>
                </div>
            </div>
        </div>
        <router-view v-on:page="setActivePage"></router-view>
    </div>
</template>

<script>
export default {
    name: 'dashboard',
    data: function(){
        return {
            authorized: false,
            nowActive: ""
        }
    },
    methods: {
        setActivePage: function(name) {
            this.nowActive = name;
        },
        logout: function() {
            localStorage.removeItem('auth-token');
            this.$router.push({name: 'Home'});
        }
    },
    beforeCreate: function() {
        var token = localStorage.getItem('auth-token');
        if (token) {
            this.$http.post('http://fhuiguide.com/api/check-token', {authToken: token})
            .then(function(response){
                var data = response.json();

                if (data.status == 'Success') {
                    this.authorized = true;
                }
            })
            .catch(function(response){
                this.$router.push({name: 'Home'});
            });
        } else {
            this.$router.push({name: 'Home'});
        }
    }
}
</script>

<style scoped>
.wrapper {
    min-height: 500px;
}

.nav {
    background-color: #912618;
    color: #FFFFFF;
    padding: 0px;
}

.nav .title {
    padding: 10px;
}

.nav h5 {
    font-family: 'Dosis', sans-serif;
    font-weight: bold;
}

.nav .nav-links {
    padding: 0px;
    padding-bottom: 20px;
}

.nav .nav-links ul {
    padding: 0px;
}

.nav .nav-links ul a {
    text-decoration: none;
    color: inherit;
}

.nav .nav-links ul a:hover
{
     text-decoration: none;
     color: inherit;
}

.nav .nav-links ul a:focus
{
     outline: none;
}

.nav .nav-links ul li {
    display: block;
    text-decoration: none;
    padding: 5px 15px 5px 15px;
    font-size: 14px;
}

.nav .nav-links ul li img {
    height: 20px;
    margin-right: 5px;
}

.nav .nav-links ul li:hover {
    background-color: #681A11;
    cursor: pointer;
}

.nav .nav-links ul li.active {
    background-color: #681A11;
}

.nav .nav-links ul li.header {
    margin-top: 5px;
    border-bottom: 1px solid #FFFFFF;
    padding-left: 7px;
    font-weight: bold;
    font-size: 12px;
}

.nav .nav-links ul li.header:hover {
    background-color: #912618;
    cursor: default;
}
</style>



