<template>
  <div class="controls col-12 col-md-10">
    <div class="title">
      <h3>Dashboard Info Lowongan Kerja</h3>
      <DataTable api="http://fhuiguide.com/api/jobs" />
    </div>
  </div>
</template>

<script>
  import DataTable from './TextCenteredControl'

  export default {
    name: 'lowker-dashboard',
    mounted: function() {
      this.$emit('page', 'lowker');
    },
    components: {
      DataTable
    }
  }
</script>

<style scoped>

  .controls {
    background-color: #F4F4F4;
    padding: 20px;
  }

  .title h3 {
    font-family: 'Dosis', sans-serif;
    font-weight: bold;
  }

</style>



