<template>
    <div id="content">
        <div class="container">
            <div class="feature-title">{{title}}</div>

            <div id="items">
                <div v-if="empty && !loading" class="col-12 text-center empty" style="margin-top: 20px;">
                    Saat ini masih belum ada {{ title }}.
                </div>
                <TextItem v-for="item in items" :title="item.title" :content="item.content" :date="item.created_at" />
            </div>

            <div v-if="loading" class="text-center" style="margin-top: 20px;"><img src="../assets/img/loading.gif" height="30" alt="loading..." /><br />Memuat data...</div>
            <FullButton v-if="showLoadMore && !loading" v-on:click.native="loadMore" text="More..." />
        </div>
    </div>
</template>

<script>
import TextItem from './TextCenteredItem'
import FullButton from './FullButton'

export default {
    name: 'text-centered-feature',
    data: function() {
        return {
            page: 1,
            items: [],
            loading: true,
            showLoadMore: false
        }
    },
    props: {
        title: {
            default: "Title"
        },
        api: {
            default: "http://localhost/"
        }
    },
    components: {
        TextItem,
        FullButton
    },
    methods: {
        loadMore: function() {
            this.page++;

            this.$http.get(this.api + '?p=' + this.page)
            .then(function(response) {
                var data = response.json();

                if (data.status == 'Success' && data.data.length > 0) {
                    this.items = this.items.concat(data.data);
                    this.showLoadMore = data.more;
                    this.loading = false;
                }
            });
        }
    },
    computed: {
        empty: function() {
            return this.items.length < 1;
        }
    },
    created: function() {
        this.$http.get(this.api)
        .then(function(response) {
            var data = response.json();
            this.showLoadMore = data.more;
            this.items = data.data;
            this.loading = false;
        });
    }
}
</script>

<style scoped>
.feature-title {
    width: 100%;
    margin-top: 20px;
    font-size: 28px;
    font-family: 'Dosis', sans-serif;
    font-weight: bold;
    color: #FFFFFF;
    border-bottom: 1px solid #FFFFFF;
}

#content {
    margin: 0px;
    background-color: #2D4452;
    padding: 1px 50px 50px 50px;
}

#items .empty {
    color: #FFFFFF;
}
</style>



