<script>
  export default {
    name: 'app',
    data: function() {
      return {
        get token() {
          return localStorage.getItem('auth_token') || '';
        },
        set token(value) {
          localStorage.setItem('auth_token', value);
        }
      }
    },
    methods: {
      logout: function(e) {
        e.preventDefault();
        this.token = '';
        localStorage.removeItem('auth_token');
        this.$router.push('/');
      }
    },
    computed: {
      authenticated: function() {
        return this.token == 'u6SHEDU&861vlOt9riL*x&@7&bP1h0-G';
      }
    }
  }
</script>

