<template>
  <div class="controls col-12 col-md-10">
    <div class="title">
      <h3>Dashboard Lapor Advo</h3>
    </div>

    <div class="row">
      <div class="control col-12">
        <table class="table table-bordered table-responsive">
          <thead>
          <tr>
            <th>Pengirim Laporan</th>
            <th>Judul Laporan</th>
            <th>Isi Laporan</th>
            <th>Pengaturan</th>
          </tr>
          </thead>
          <tbody>
          <tr v-if="!loading" v-for="advoReport in advoReports">
            <td width="15%">{{ advoReport.sender }}</td>
            <td width="20%">{{ advoReport.title }}</td>
            <td width="50%">{{ advoReport.report }}</td>
            <td style="text-align: center;" width="15%">
              <a :href="api + '/..' + advoReport.image" target="_blank" class="btn btn-sm btn-primary" v-if="advoReport.image != ''">Lihat Gambar</a>
              <button class="btn btn-sm btn-danger" data-toggle="modal" data-target="#deleteModal" v-on:click="select(advoReport.id)" v-if="!deleting || selectedPost != advoReport.id">Hapus</button>
              <img v-else-if="deleting && selectedPost == advoReport.id" src="../assets/img/loading.gif" height="30" alt="loading..." />
            </td>
          </tr>
          <tr v-if="loading">
            <td colspan="5" class="text-center">
              <img src="../assets/img/loading.gif" height="30" alt="loading..." /><br />Memuat data...
            </td>
          </tr>
          <tr v-if="tooMany">
            <td colspan="5" class="text-center">
              <button class="btn btn-sm btn-block btn-secondary" v-on:click="loadMore">Lihat lebih banyak</button>
            </td>
          </tr>
          <tr v-if="empty">
            <td colspan="5" class="text-center">
              Belum ada laporan yang masuk.
            </td>
          </tr>
          </tbody>
        </table>
      </div>
    </div>

    <div class="modal fade" id="deleteModal" tabindex="-1" role="dialog" aria-labelledby="deleteModal" aria-hidden="true">
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title">Hapus Post</h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close" v-on:click="unselect">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <p>Apakah kamu yakin akan menghapus laporan tersebut?</p>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-danger" data-dismiss="modal" v-on:click="deleteSelected">Hapus</button>
            <button type="button" class="btn btn-secondary" data-dismiss="modal" v-on:click="unselect">Batal</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'lapor-advo-dashboard',
    mounted: function() {
      this.$emit('page', 'lapor');
      $('#datetimepicker').datetimepicker();
    },
    data: function() {
      return {
        api: 'http://fhuiguide.com/api/advo_reports',
        advoReports: [],
        tooMany: false,
        page: 1,
        loading: true,
        sending: false,
        selectedPost: null,
        deleting: false,
      }
    },
    methods: {
      loadReports: function() {
        this.loading = true;

        this.$http.get(this.api)
          .then(function(response){
            var data = response.json();

            if (data.status == 'Success') {

              this.advoReports = data.data;
              this.tooMany = data.more;

            } else if (data.status == 'Error') {
              alert('Terjadi kesalahan! ' + data.message);
            } else {
              alert('Terjadi kesalahan!');
            }

            this.page = 1;
            this.loading = false;
          })
          .catch(function(response){
            alert('Terjadi kesalahan!');
            this.loading = false;
          });

      },
      loadMore: function() {
        this.page++;
        this.loading = true;

        this.$http.get(this.api + '?p=' + this.page)
          .then(function(response){
            var data = response.json();

            if (data.status == 'Success') {

              this.advoReports = this.advoReports.concat(data.data);
              this.tooMany = data.more;

            } else if (data.status == 'Error') {
              alert('Terjadi kesalahan! ' + data.message);
            } else {
              alert('Terjadi kesalahan!');
            }

            this.loading = false;
          })
          .catch(function(response){
            alert('Terjadi kesalahan!');
          });

      },
      select: function(id) {
        this.selectedPost = id;
      },
      deleteSelected: function() {
        this.deleting = true;
        this.$http.delete(this.api + '/' + this.selectedPost, {headers: {Authorization: localStorage.getItem('auth-token')}})
          .then(function(response) {
            var data = response.json();

            if (data.status == 'Success') {
              this.loadReports();
            }

            this.deleting = false;
          });
      },
      unselect: function() {
        this.selectedPost = null;
      }
    },
    created: function() {
      this.loadReports();
    },
    computed: {
      empty: function() {
        return !this.loading && this.advoReports.length < 1;
      }
    }
  }

</script>

<style scoped>

  th {
    text-align: center;
  }

  .controls {
    background-color: #F4F4F4;
    padding: 20px;
  }

  .title h3 {
    font-family: 'Dosis', sans-serif;
    font-weight: bold;
  }

  .control {
    margin-top: 10px;
    padding: 10px;
    background-color: #FFFFFF;
  }

</style>



