<template>
    <div class="item">
        <div class="row">
            <div class="col-12 item-title">
                {{title}}<br />
                <span class="date">Diterbitkan pada {{readableDate(date)}}</span>
            </div>
        </div>
        <div class="row">
            <div class="col-12 item-text" v-html="content">

            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: "picture-item",
    props: {
        title: {
            default: "Item Title"
        },
        content: {
            default: "Lorem ipsum"
        },
        date: {}
    },
    methods: {
        readableDate: function(date) {
            var dateTime = date.split(' ');

            var date = dateTime[0];
            var time = dateTime[1];

            var dateComponent = date.split('-');
            var year = dateComponent[0];
            var month = parseInt(dateComponent[1]);
            var day = parseInt(dateComponent[2]);

            var timeComponent = time.split(':');
            var hour = parseInt(timeComponent[0])+7;
            var minute = timeComponent[1];

            var months = ['', 'Januari', 'Februari', 'Maret', 'April', 'Mei', 'Juni', 'Juli', 'Agustus', 'September', 'Oktober', 'November', 'Desember'];

            var date = day + ' ' + months[month] + ' ' + year + ' ' + hour + ':' + minute;

            return date;
        }
    }
}
</script>

<style scoped>
.item {
    min-height: 50px;
    background-color: #a8a8a8;
    margin-top: 20px;
}

.item .item-title {
    padding: 15px;
    color: #2D4452;
    font-weight: bold;
}

.item .item-title .date {
    font-size: 12px;
}

.item .item-text {
    background-color: #F5F5F5;
    padding: 20px;
}
</style>



