<template>
<button class="btn btn-default">{{text}}</button>
</template>

<script>
export default {
    name: 'full-button',
    props: {
        text: {
            default: 'Button'
        }
    },
}
</script>

<style scoped>
button {
    margin-top: 20px;
    width: 100%;
}
</style>



