<template>
<div>
    <div class="row">
        <div class="control col-12">
            <div class="name">Semua Rancangan Kuliah</div>

            <table class="table table-bordered table-responsive">
                <thead>
                    <th>Nama Jenis Mata Kuliah</th>
                    <th>Pengaturan</th>
                </thead>
                <tbody>
                    <tr v-if="noPost && !loading" class="text-center">
                        <td colspan="3">Belum ada rancangan kuliah yang dibuat.</td>
                    </tr>
                    <tr v-for="post in posts" v-if="post.id != 'deleted'">
                        <td>{{ post.name }}</td>
                        <td>
                            <!--button class="btn btn-sm btn-primary">Edit</button-->
                            <button class="btn btn-sm btn-danger" data-toggle="modal" data-target="#deleteModal" v-on:click="select(post.id)">Hapus</button>
                            <a :href="api + '/..' + post.design" target="_blank" class="btn btn-sm btn-primary" v-if="post.design != undefined">Lihat Rancangan Kuliah</a>
                        </td>
                    </tr>
                    <tr v-else>
                        <td colspan="3">Jenis mata kuliah tersebut telah dihapus.</td>
                    </tr>
                    <tr v-if="loading">
                        <td colspan="3" class="text-center">
                            <img src="../assets/img/loading.gif" height="30" alt="loading..." /><br />Memuat data...
                        </td>
                    </tr>
                </tbody>
            </table>
        </div>
    </div>
    <div class="row">
        <div class="control col-12">
            <div class="name">Tambah Peminatan dan Rancangan Kuliah Baru</div>
            <form>
                <div v-if="!sending">
                    <div class="form-group">
                        <input type="text" class="form-control input-sm" placeholder="Nama jenis mata kuliah" v-model="new_name" required>
                    </div>
                    <div class="form-group">
                        <label for="new-design" style="font-size: 14px;">Upload rancangan kuliah (boleh kosong)</label>
                        <input type="file" class="form-control input-sm" v-on:change="setDesign($event)" id="new-design" required>
                    </div>
                    <div class="form-check">
                        <label class="form-check-label">
                            <input class="form-check-input" type="checkbox" v-model="new_mandatory">
                            Mata Kuliah Wajib
                        </label>
                    </div>
                    <button type="submit" class="btn btn-secondary btn-block" v-on:click="createPost($event)" :disabled="new_name == ''">Simpan</button>
                </div>
                <div class="col-12 text-center" v-else><img src="../assets/img/loading.gif" height="30" alt="loading..." /><br />Sedang mengirimkan data...</div>
            </form>
        </div>
    </div>

    <div class="modal fade" id="deleteModal" tabindex="-1" role="dialog" aria-labelledby="deleteModal" aria-hidden="true">
        <div class="modal-dialog" role="document">
            <div class="modal-content">
                <div class="modal-header">
                    <h5 class="modal-title">Hapus Rancangan Kuliah</h5>
                    <button type="button" class="close" data-dismiss="modal" aria-label="Close" v-on:click="removeSelected()">
                        <span aria-hidden="true">&times;</span>
                    </button>
                </div>
                <div class="modal-body">
                    <p>Apakah kamu yakin akan menghapus rancangan kuliah tersebut?</p>
                </div>
                <div class="modal-footer">
                    <button type="button" class="btn btn-danger" data-dismiss="modal" v-on:click="deletePost()">Hapus</button>
                    <button type="button" class="btn btn-secondary" data-dismiss="modal" v-on:click="removeSelected()">Batal</button>
                </div>
            </div>
        </div>
    </div>
</div>
</template>

<script>

export default {
    name: 'rancangan-kuliah-control',
    data: function() {
        return {
            posts: [],
            selectedPost: null,
            new_name: '',
            new_design: null,
            new_mandatory: false,
            sending: false,
            loading: true
        }
    },
    props: {
        api: {
            default: "http://localhost/"
        }
    },
    methods: {
        setDesign: function(e) {
            var files = e.target.files || e.dataTransfer.files;
            if (!files.length) return;
            this.new_design = files[0];
        },
        createPost: function(e) {
            e.preventDefault();

            this.sending = true;

            var formData = new FormData();

            formData.append('name', this.new_name);
            if (this.new_design != null) formData.append('design', this.new_design);
            formData.append('mandatory', this.new_mandatory ? 1 : 0);

            this.$http.post(this.api, formData, {headers: {Authorization: localStorage.getItem('auth-token')}})
            .then(function(response) {
                var data = response.json();

                if (data.status == 'Success') {
                    this.posts.push(data.data[0]);

                    this.new_image_title = "";
                    $('#new-image').val("");

                } else if (data.status == 'Unauthorized') {
                    alert('Sepertinya Anda belum login. Coba login ulang.');

                } else if (data.status == 'Error') {
                    alert('Terjadi kesalahan! ' + data.message);

                } else {
                    alert('Terjadi kesalahan!');
                }

                this.new_name = '';
                this.new_design = null;
                this.new_mandatory = false;

                this.sending = false;
            }).catch(function(error){
                alert("Terjadi kesalahan! Mungkin ukuran file terlalu besar?");
                console.log(error);
                this.sending = false;
            });
        },
        deletePost: function() {
            this.$http.delete(this.api + '/' + this.selectedPost, {headers: {Authorization: localStorage.getItem('auth-token')}})
            .then(function(response) {
                var data = response.json();

                if (data.status == 'Success') {
                    for (var i = 0; i < this.posts.length; i++) {
                        if (this.posts[i].id == this.selectedPost) {
                            this.posts[i].id = "deleted";
                        }
                    }
                } else if (data.status == 'Unauthorized') {
                    alert('Sepertinya Anda belum login. Coba login ulang.');
                }
            });
        },
        select: function(id) {
            this.selectedPost = id;
        },
        removeSelected: function() {
            this.selectedPost = null;
        }
    },
    created: function() {
        this.$http.get(this.api)
        .then(function(response){
            var data = response.json();

            this.posts = data.data;
            this.loading = false;
        });
    },
    computed: {
        noPost: function() {
            return this.posts.length < 1;
        }
    }
}

</script>

<style scoped>
.control {
    margin-top: 10px;
    padding: 10px;
    background-color: #FFFFFF;
}

.control .name {
    border-bottom: 1px solid #E9ECEF;
    margin-bottom: 5px;
}

.control form {
    padding: 10px;
    border: 1px solid #E9ECEF;
}
</style>



