<template>
    <div class="wrapper col-12 col-md-10">
      <br>
      <div class="row alert alert-success alert-dismissable" v-if="updated">
        Info terbaru berhasil di-<i>update</i>
      </div>
        <div class="row">
            <div class="latest-info col-12">
                <h2>Info Terbaru</h2>
                <form>
                    <div class="form-group">
                    <textarea class="form-control info_terbaru" rows="16" :disabled="loading" v-model="info"></textarea>
                    </div>

                    <button type="submit" class="btn btn-sm btn-primary" :disabled="loading" v-if="!updating" v-on:click="updateInfo">Update</button>
                    <div v-else class="text-center" style="margin-top: 20px; color: white;"><img src="../assets/img/loading.gif" height="30" alt="loading..." /></div>
                </form>

            </div>
        </div>

        <div class="row">
            <div class="navigation col-12">
                <h2>Navigasi</h2>
                <div class="row">

                    <div class="navi col-12 col-md-4">
                        Pengembangan Karir
                        <ul>
                            <li><router-link :to="{ name: 'Dashboard Info Lowker' }">Info Lowongan kerja</router-link></li>
                            <li><router-link :to="{ name: 'Dashboard Pengkarpedia' }">Pengkarpedia</router-link></li>
                            <li><router-link :to="{ name: 'Dashboard Info Magang' }">Info Magang</router-link></li>
                            <li><router-link :to="{ name: 'Dashboard Company Profile' }">Company Profile</router-link></li>
                            <!--li><router-link :to="{ name: 'Dashboard Drop Your CV' }">Drop Your CV</router-link></li-->
                        </ul>
                    </div>

                    <div class="navi col-12 col-md-4">
                        Pendidikan &amp; Keilmuan
                        <ul>
                            <li><router-link :to="{ name: 'Dashboard Rancangan Kuliah' }">Rancangan Kuliah</router-link></li>
                            <li><router-link :to="{ name: 'Dashboard Info Tugas' }">Info Tugas</router-link></li>
                            <li><router-link :to="{ name: 'Dashboard Bahan Kuliah' }">Bahan Kuliah</router-link></li>
                            <li><router-link :to="{ name: 'Dashboard Fasilitator Lomba' }">Fasilitator Lomba</router-link></li>
                            <li><router-link :to="{ name: 'Dashboard PKM Guide' }">PKM Guide</router-link></li>
                            <li><router-link :to="{ name: 'Dashboard Bank Soal' }">Bank Soal</router-link></li>
                        </ul>
                    </div>

                    <div class="navi col-12 col-md-4">
                        Advokasi
                        <ul>
                            <li><router-link :to="{ name: 'Dashboard Advografis' }">Advografis</router-link></li>
                            <li><router-link :to="{ name: 'Dashboard Info BOP' }">Info BOP</router-link></li>
                            <li><router-link :to="{ name: 'Dashboard Info Beasiswa' }">Info Beasiswa</router-link></li>
                        </ul>
                    </div>

                </div>
            </div>
        </div>

    </div>
</template>

<script>

export default {
    name: 'home-dashboard',
    data: function() {
        return {
            loading: true,
            info: 'Loading...',
            updating: false,
            updated: false
        }
    },
    methods: {
        updateInfo: function() {
            this.updating = true;
            this.$http.put('http://fhuiguide.com/api/info', {info: this.info}, {headers: {Authorization: localStorage.getItem('auth-token')}})
            .then(function(response){
                var data = response.json();

                if (data.status == 'Success') {
                    this.info = data.data[0].info;
                    this.updated = true;
                } else {
                    alert('Terjadi kesalahan! ' + data.message);
                }

                this.updating = false;
            })
            .catch(function(response){
                alert('Terjadi kesalahan! ' + response.message);
                this.updating = false;
            });
        }
    },
    created: function() {
        this.$http.get('http://fhuiguide.com/api/info')
        .then(function(response) {
            var data = response.json();

            if (data.status == 'Success') {
                this.info = data.data[0].info;

                this.loading = false;
            } else {
                alert('Terjadi kesalahan! ' + data.message);
                this.info = 'Error!';
            }

        })
        .catch(function(response){
            this.info = 'Error!';
            alert('Terjadi kesalahan!');
        });
    }
}

</script>

<style scoped>

.wrapper {
    background-color: #F4F4F4;
}

h2 {
    font-family: 'Dosis', sans-serif;
    font-weight: bold;
}

.latest-info, .navigation {
    background-color: #FFFFFF;
    margin-top: 20px;
    padding: 20px;
}

.navigation>.row {
    margin-top: 20px;
}

  .info_terbaru{

  }
</style>



