<template>
    <div class="controls col-12 col-md-10">
        <div class="title">
            <h3>Dashboard Bahan Kuliah</h3>
            <DataTable api="http://fhuiguide.com/api/course_materials" />
        </div>
    </div>
</template>

<script>
    import DataTable from './CoursesCenteredControl'

    export default {
        name: 'bahan-kuliah-dashboard',
        mounted: function() {
            this.$emit('page', 'bahan-kuliah');
        },
        components: {
            DataTable
        }
    }
</script>

<style scoped>

.controls {
    background-color: #F4F4F4;
    padding: 20px;
}

.title h3 {
    font-family: 'Dosis', sans-serif;
    font-weight: bold;
}

</style>



