<template>
<div>
    <div class="row">
        <div class="control col-12">
            <div class="name">Semua</div>
            <select class="form-control" v-model="selected_course" v-on:change="loadMaterials" style="margin-bottom: 10px;">
                <option disabled value="">Pilih mata kuliah...</option>
                <option v-for="course in courses" :value="course.id">{{ course.name }}</option>
            </select>

            <table class="table table-bordered table-responsive">
                <thead>
                    <th>Judul</th>
                    <th>Nama Berkas</th>
                    <th>Tanggal Upload</th>
                    <th>Pengaturan</th>
                </thead>
                <tbody>
                    <tr v-if="noPost && !loading" class="text-center">
                        <td colspan="4">Tidak ada berkas di sini.</td>
                    </tr>
                    <tr v-for="post in posts" v-if="post.id != 'deleted' && !loading">
                        <td>{{ post.title }}</td>
                        <td>{{ post.filename }}</td>
                        <td>{{ post.created_at }}</td>
                        <td>
                            <!--button class="btn btn-sm btn-primary">Edit</button-->
                            <button class="btn btn-sm btn-danger" data-toggle="modal" data-target="#deleteModal" v-on:click="select(post.id)">Hapus</button>
                            <a :href="api + '/..' + post.file" class="btn btn-sm btn-primary" target="_blank">Lihat Berkas</a>
                        </td>
                    </tr>
                    <tr v-else-if='!loading'>
                        <td colspan="4">Berkas telah dihapus.</td>
                    </tr>
                    <tr v-if="loading">
                        <td colspan="4" class="text-center">
                            <img src="../assets/img/loading.gif" height="30" alt="loading..." /><br />Memuat data...
                        </td>
                    </tr>
                    <tr v-if="tooMany && !loading">
                        <td colspan="4">
                            <button class="btn btn-secondary btn-block" v-on:click="loadMore()">Lihat lebih banyak...</button>
                        </td>
                    </tr>
                </tbody>
            </table>
        </div>
    </div>
    <div class="row" v-if="selected_course != ''">
        <div class="control col-12">
            <div class="name">Tambah Berkas Baru</div>
            <form>
                <div v-if="!sending">
                    <div class="form-group">
                        <input type="text" class="form-control input-sm" placeholder="Judul" v-model="new_file_title" required>
                    </div>
                    <div class="form-group">
                        <input type="file" class="form-control input-sm" v-on:change="setFile($event)" id="new-file" required>
                    </div>
                    <button type="submit" class="btn btn-secondary btn-block" v-on:click="storeFile($event)" :disabled="new_file_title == '' || new_file == null">Upload</button>
                </div>
                <div class="col-12 text-center" v-else><img src="../assets/img/loading.gif" height="30" alt="loading..." /><br />Uploading...</div>
            </form>
        </div>
    </div>

    <MatkulManager api="http://fhuiguide.com/api/courses"></MatkulManager>

    <div class="modal fade" id="deleteModal" tabindex="-1" role="dialog" aria-labelledby="deleteModal" aria-hidden="true">
        <div class="modal-dialog" role="document">
            <div class="modal-content">
                <div class="modal-header">
                    <h5 class="modal-title">Hapus Post</h5>
                    <button type="button" class="close" data-dismiss="modal" aria-label="Close" v-on:click="removeSelected()">
                        <span aria-hidden="true">&times;</span>
                    </button>
                </div>
                <div class="modal-body">
                    <p>Apakah kamu yakin akan menghapus post tersebut?</p>
                </div>
                <div class="modal-footer">
                    <button type="button" class="btn btn-danger" data-dismiss="modal" v-on:click="deletePost()">Hapus</button>
                    <button type="button" class="btn btn-secondary" data-dismiss="modal" v-on:click="removeSelected()">Batal</button>
                </div>
            </div>
        </div>
    </div>
</div>
</template>

<script>
import MatkulManager from './MatkulManager'

var striptags = require('striptags');

export default {
    name: 'courses-centered-control',
    data: function() {
        return {
            posts: [],
            courses: [],
            page : 1,
            tooMany: false,
            selectedPost: null,
            new_file_title: '',
            new_file: null,
            selected_course: '',
            sending: false,
            loading: true
        }
    },
    props: {
        api: {
            default: "http://localhost/"
        }
    },
    methods: {
        setFile: function(e) {
            var files = e.target.files || e.dataTransfer.files;
            if (!files.length) return;
            this.new_file = files[0];
        },
        storeFile: function(e) {
            e.preventDefault();

            this.sending = true;

            var formData = new FormData();

            formData.append('title', this.new_file_title);
            formData.append('course', this.selected_course);
            formData.append('file', this.new_file);

            this.$http.post(this.api, formData, {headers: {Authorization: localStorage.getItem('auth-token')}})
            .then(function(response) {
                var data = response.json();

                if (data.status == 'Success') {
                    this.posts.unshift(data.data[0]);
                    $('#new-file').val("");
                } else if (data.status == 'Unauthorized') {
                    alert('Sepertinya Anda belum login. Coba login ulang.');
                } else {
                    alert('Terjadi kesalahan.');
                }

                this.new_file_title = '';
                this.new_course = '';
                this.new_file = null;

                this.sending = false;
            }).catch(function(error){
                alert("Terjadi kesalahan! Mungkin ukuran file terlalu besar?");
                console.log(error);
                this.sending = false;
            });
        },
        deletePost: function() {
            this.$http.delete(this.api + '/' + this.selectedPost, {headers: {Authorization: localStorage.getItem('auth-token')}})
            .then(function(response) {
                var data = response.json();

                if (data.status == 'Success') {
                    for (var i = 0; i < this.posts.length; i++) {
                        if (this.posts[i].id == this.selectedPost) {
                            this.posts[i].id = "deleted";
                        }
                    }
                } else if (data.status == 'Unauthorized') {
                    alert('Sepertinya Anda belum login. Coba login ulang.');
                }
            });
        },
        loadMore: function(){
            this.page++;
            this.loading = true;

            this.$http.get(this.api + '?p=' + this.page)
            .then(function(response) {
                var data = response.json();

                if (data.status == 'Success' && data.data.length > 0) {
                    this.tooMany = data.more;
                    this.posts = this.posts.concat(data.data);
                } else {
                    console.log(data);
                }
                this.loading = false;
            });
        },
        select: function(id) {
            this.selectedPost = id;
        },
        removeSelected: function() {
            this.selectedPost = null;
        },
        loadMaterials: function() {
            this.loading = true;
            var filter = this.selected_course != '' ? '?c=' + this.selected_course : '';

            this.$http.get(this.api + filter)
            .then(function(response){
                var data = response.json();

                this.posts = data.data;
                this.tooMany = data.more;

                this.$http.get('http://fhuiguide.com/api/courses')
                .then(function(response2){
                    var data2 = response2.json();

                    this.courses = data2.data;

                    this.loading = false;
                });
            });
        }
    },
    created: function() {
        this.loadMaterials();
    },
    computed: {
        noPost: function() {
            return this.posts.length < 1;
        }
    },
    components: {
        MatkulManager
    }
}

</script>

<style scoped>
.control {
    margin-top: 10px;
    padding: 10px;
    background-color: #FFFFFF;
}

.control .name {
    border-bottom: 1px solid #E9ECEF;
    margin-bottom: 5px;
}

.control form {
    padding: 10px;
    border: 1px solid #E9ECEF;
}
</style>



