<template>
    <div class="controls col-12 col-md-10">
        <div class="title">
            <h3>Dashboard Rancangan Kuliah</h3>
            <Control api="http://fhuiguide.com/api/course_plans" />
        </div>
    </div>
</template>

<script>
    import Control from './RancanganKuliahControl'

    export default {
        name: 'lowker-dashboard',
        mounted: function() {
            this.$emit('page', 'rancangan-kuliah');
        },
        components: {
            Control
        }
    }
</script>

<style scoped>

.controls {
    background-color: #F4F4F4;
    padding: 20px;
}

.title h3 {
    font-family: 'Dosis', sans-serif;
    font-weight: bold;
}

</style>



