import Vue from 'vue'
import Router from 'vue-router'

import Home from '@/components/Home'
import Login from '@/components/Login'

import Dashboard from '@/components/Dashboard'
import DashboardAdvografis from '@/components/DashboardAdvografis'
import DashboardBOP from '@/components/DashboardBop'
import DashboardLaporAdvo from '@/components/DashboardLaporAdvo'
import DashboardBeasiswa from '@/components/DashboardBeasiswa'
import DashboardLowker from '@/components/DashboardLowker'
import DashboardPengkarpedia from '@/components/DashboardPengkarpedia'
import DashboardMagang from '@/components/DashboardMagang'
import DashboardCompany from '@/components/DashboardCompany'
import DashboardLomba from '@/components/DashboardLomba'
import DashboardPKM from '@/components/DashboardPKM'
import DashboardRancanganKuliah from '@/components/DashboardRancanganKuliah'
import DashboardBahanKuliah from '@/components/DashboardBahanKuliah'
import DashboardBankSoal from '@/components/DashboardBankSoal'
import DashboardInfoTugas from '@/components/DashboardInfoTugas'
import DashboardHome from '@/components/DashboardHome'

import LaporAdvoForm from '@/components/LaporAdvoForm'

import Advografis from '@/components/Advografis'
import InfoBop from '@/components/InfoBop'
import InfoBeasiswa from '@/components/InfoBeasiswa'
import InfoLowker from '@/components/InfoLowker'
import InfoMagang from '@/components/InfoMagang'
import CompanyProfile from '@/components/CompanyProfile'
import Pengkarpedia from '@/components/Pengkarpedia'

import Lomba from '@/components/Lomba'
import PKM from '@/components/PKM'
import RancanganKuliah from '@/components/RancanganKuliah'
import BahanKuliah from '@/components/BahanKuliah'
import BankSoal from '@/components/BankSoal'
import InfoTugas from '@/components/InfoTugas'

Vue.use(Router)

export default new Router({
    routes: [
        {
            path: '/',
            name: 'Home',
            component: Home
        },
        {
            path: '/admin',
            name: 'Login',
            component: Login
        },
        {
            path: '/dashboard',
            name: 'Dashboard',
            component: Dashboard,
            children: [
                {
                    name: 'Dashboard Home',
                    path: '/',
                    component: DashboardHome
                },
                {
                    name: 'Dashboard Advografis',
                    path: 'advografis',
                    component: DashboardAdvografis
                },
                {
                    name: 'Dashboard Info BOP',
                    path: 'bop',
                    component: DashboardBOP
                },
                {
                  name: 'Dashboard Lapor Advo',
                  path: 'lapor',
                  component: DashboardLaporAdvo
                },
                {
                    name: 'Dashboard Info Beasiswa',
                    path: 'beasiswa',
                    component: DashboardBeasiswa
                },
                {
                    name: 'Dashboard Info Lowker',
                    path: 'lowker',
                    component: DashboardLowker
                },
                {
                    name: 'Dashboard Pengkarpedia',
                    path: 'pengkarpedia',
                    component: DashboardPengkarpedia
                },
                {
                    name: 'Dashboard Info Magang',
                    path: 'magang',
                    component: DashboardMagang
                },
                {
                  name: 'Dashboard Company Profile',
                  path: 'company',
                  component: DashboardCompany
                },
                {
                    name: 'Dashboard Fasilitator Lomba',
                    path: 'lomba',
                    component: DashboardLomba
                },
                {
                    name: 'Dashboard PKM Guide',
                    path: 'pkm',
                    component: DashboardPKM
                },
                {
                    name: 'Dashboard Rancangan Kuliah',
                    path: 'rancangankuliah',
                    component: DashboardRancanganKuliah
                },
                {
                    name: 'Dashboard Bahan Kuliah',
                    path: 'bahankuliah',
                    component: DashboardBahanKuliah
                },
                {
                    name: 'Dashboard Bank Soal',
                    path: 'soal',
                    component: DashboardBankSoal
                },
                {
                    name: 'Dashboard Info Tugas',
                    path: 'tugas',
                    component: DashboardInfoTugas
                },
            ]
        },
        {
            path: '/advografis',
            name: 'Advografis',
            component: Advografis
        },
        {
            path: '/bop',
            name: 'InfoBOP',
            component: InfoBop
        },
        {
            path: '/beasiswa',
            name: 'InfoBeasiswa',
            component: InfoBeasiswa
        },
        {
            path: '/lowker',
            name: 'InfoLowker',
            component: InfoLowker
        },
        {
            path: '/magang',
            name: 'InfoMagang',
            component: InfoMagang
        },
        {
          path: '/company',
          name: 'CompanyProfile',
          component: CompanyProfile
        },
        {
            path: '/pengkarpedia',
            name: 'Pengkarpedia',
            component: Pengkarpedia
        },
        {
            path: '/lapor',
            name: 'LaporAdvoForm',
            component: LaporAdvoForm
        },
        {
            path: '/lomba',
            name: 'Lomba',
            component: Lomba
        },
        {
            path: '/pkm',
            name: 'PKM',
            component: PKM
        },
        {
            path: '/rancangankuliah',
            name: 'Rancangan Kuliah',
            component: RancanganKuliah
        },
        {
            path: '/bahankuliah',
            name: 'Bahan Kuliah',
            component: BahanKuliah
        },
        {
            path: '/soal',
            name: 'Bank Soal',
            component: BankSoal
        },
        {
            path: '/tugas',
            name: 'Info Tugas',
            component: InfoTugas
        },
    ]
})



