<template>
    <div class="controls col-12 col-md-10">
        <div class="title">
            <h3>Dashboard Fasilitator Lomba</h3>
            <DataTable api="http://fhuiguide.com/api/competitions" />
        </div>
    </div>
</template>

<script>
    import DataTable from './TextCenteredControl'

    export default {
        name: 'lomba-dashboard',
        mounted: function() {
            this.$emit('page', 'lomba');
        },
        components: {
            DataTable
        }
    }
</script>

<style scoped>

.controls {
    background-color: #F4F4F4;
    padding: 20px;
}

.title h3 {
    font-family: 'Dosis', sans-serif;
    font-weight: bold;
}

</style>



