<template>
    <div class="wrapper">
      <br><br>
        <div class="lapor container">

            <div class="row alert alert-success alert-dismissable" v-if="updated">
              Laporan anda telah berhasil dikirim.
            </div>
            <div class="row">
                <div class="title col-12 container">
                    Lapor Advo
                </div>
            </div>
            <div class="row">
                <div class="form col-12 container">
                    <form v-if="!sending">
                      <div class="form-group">
                        <input type="text" class="form-control input-sm" placeholder="Nama Pengirim" v-model="new_report_sender" required>
                      </div>
                      <div class="form-group">
                        <input type="text" class="form-control input-sm" placeholder="Judul Laporan" v-model="new_report_title" required>
                      </div>
                      <div class="form-group">
                        <textarea rows="8" class="form-control input-sm" placeholder="Isi Laporan" v-model="new_report" required></textarea>
                      </div>
                      <div class="form-group">
                        <input type="file" class="form-control input-sm" v-on:change="setImage($event)" id="new-design" required>
                      </div>
                      <button type="submit" class="btn btn-secondary btn-block" v-on:click="createReport($event)" :disabled="new_report_sender == '' || new_report_title == '' || new_report == ''">Kirim Laporan</button>
                    </form>
                    <div v-else="loading" class="text-center" style="margin-top: 20px;"><img src="../assets/img/loading.gif" height="30" alt="loading..." /><br />Sedang mengirimkan laporan...</div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>

export default {
  name: 'lapor-advo-form',
  data: function() {
    return {
      posts: [],
      new_report_sender: '',
      new_report_title: '',
      new_report: '',
      new_image: null,
      sending: false,
      loading: true,
      updated: false
    }
  },
  props: {
    api: {
      default: "http://fhuiguide.com/api/advo_reports"
    }
  },
  methods: {
    setImage: function(e) {
      var files = e.target.files || e.dataTransfer.files;
      if (!files.length) return;
      this.new_image = files[0];
    },
    createReport: function(e) {
      e.preventDefault();

      this.sending = true;

      var formData = new FormData();

      formData.append('sender', this.new_report_sender);
      formData.append('title', this.new_report_title);
      formData.append('report', this.new_report);
      formData.append('image', this.new_image);

      this.$http.post(this.api, formData, {headers: {Authorization: localStorage.getItem('auth-token')}})
        .then(function(response) {
          var data = response.json();

          if (data.status == 'Success') {
            this.posts.push(data.data[0]);
            this.updated = true;

            this.new_report_sender = "";
            this.new_report_title = "";
            this.new_report = "";
            this.new_image = "";
            $('#new-image').val("");

          } else if (data.status == 'Unauthorized') {
            alert('Sepertinya Anda belum login. Coba login ulang.');

          } else if (data.status == 'Error') {
            alert('Terjadi kesalahan! ' + data.message);

          } else {
            alert('Terjadi kesalahan!');
          }

          this.sending = false;
        }).catch(function(error){
        alert("Terjadi kesalahan! Mungkin ukuran file terlalu besar?");
        console.log(error.toString());
        this.sending = false;
        });
    }
  }
}

</script>

<style scoped>

.wrapper {
    background-color: #2D4452;
    padding: 20px;
}

.lapor.container {
    background-color: #f5f8fa;
    padding: 10px;
    color: #101010;
}

.title {
    font-family: 'Dosis', sans-serif;
    font-weight: bold;
    font-size: 24px;
    border-bottom: 1px solid #FFFFFF;
}

.form {
    margin-top: 10px;
    font-weight: bold;
}

.form .form-group.row {
    margin-top: 10px;
}

</style>



