<template>
<div>
    <div id="accordion" role="tablist">
        <div class="card">
            <div class="card-header" role="tab" id="headingOne">
                <h5 class="mb-0">
                    <a data-toggle="collapse" href="#collapseOne" aria-expanded="true" aria-controls="collapseOne" class="btn btn-block btn-sm btn-primary">
                        Tambah/Edit Mata Kuliah
                    </a>
                </h5>
            </div>

            <div id="collapseOne" class="collapse" role="tabpanel" aria-labelledby="headingOne" data-parent="#accordion">
                <div class="card-body">

                    <!-- MAIN -->
                    <table class="table table-bordered table-responsive">
                        <thead>
                            <th>Nama Mata Kuliah</th>
                            <th>Kategori</th>
                            <th>Pengaturan</th>
                        </thead>
                        <tbody>
                            <tr v-if="noCourses && !loading" class="text-center">
                                <td colspan="3">Belum ada mata kuliah.</td>
                            </tr>
                            <tr v-for="course in courses" v-if="course.id != 'deleted'">
                                <td>{{ course.name }}</td>
                                <td>{{ course.category }}</td>
                                <td>
                                    <!--button class="btn btn-sm btn-primary">Edit</button-->
                                    <button class="btn btn-sm btn-danger" data-toggle="modal" data-target="#deleteModalCourse" v-on:click="select(course.id)">Hapus</button>
                                </td>
                            </tr>
                            <tr v-else-if='deleted'>
                                <td colspan="3">Mata kuliah ini telah dihapus.</td>
                            </tr>
                            <tr v-if="loading">
                                <td colspan="3" class="text-center">
                                    <img src="../assets/img/loading.gif" height="30" alt="loading..." /><br />Memuat data...
                                </td>
                            </tr>
                            <tr v-if="tooMany && !loading">
                                <td colspan="3">
                                    <button class="btn btn-secondary btn-block" v-on:click="loadMore()">Lihat lebih banyak...</button>
                                </td>
                            </tr>
                        </tbody>
                    </table>

                    <div class="addnew" v-if="!loading">
                        <div v-if="!sending">
                            <h6><u>Tambah Mata Kuliah Baru</u></h6>
                            <form>
                                <div class="form-group">
                                    <input type="text" class="form-control" placeholder="Nama mata kuliah" v-model="new_name" required />
                                </div>
                                <div class="form-group">
                                    <select class="form-control" v-model="new_type" required>
                                        <option disabled value="">Pilih jenis mata kuliah...</option>
                                        <option v-for="plan in coursePlans" :value="plan.id">{{ plan.name }}</option>
                                    </select>
                                </div>
                                <div class="form-group">
                                    <button type="submit" class="btn btn-primary" v-on:click="createCourse" :disabled='invalid'>Tambah</button>
                                </div>
                            </form>
                        </div>
                        <div class="text-center" v-else>
                            <img src="../assets/img/loading.gif" height="30" alt="loading..." /><br />Membuat mata kuliah baru...
                        </div>
                    </div>

                  <div class="modal fade" id="deleteModalCourse" tabindex="-1" role="dialog" aria-labelledby="deleteModalCourse" aria-hidden="true">
                    <div class="modal-dialog" role="document">
                      <div class="modal-content">
                        <div class="modal-header">
                          <h5 class="modal-title">Hapus Post</h5>
                          <button type="button" class="close" data-dismiss="modal" aria-label="Close" v-on:click="removeSelected()">
                            <span aria-hidden="true">&times;</span>
                          </button>
                        </div>
                        <div class="modal-body">
                          <p>Apakah kamu yakin akan menghapus mata kuliah tersebut?</p>
                        </div>
                        <div class="modal-footer">
                          <button type="button" class="btn btn-danger" data-dismiss="modal" v-on:click="deleteCourse()">Hapus</button>
                          <button type="button" class="btn btn-secondary" data-dismiss="modal" v-on:click="removeSelected()">Batal</button>
                        </div>
                      </div>
                    </div>
                  </div>

                </div>
            </div>
        </div>
    </div>
</div>
</template>

<script>

export default {
    name: "matkul-manager",
    props: {
        api: {
            default: "http://fhuiguide.com/api"
        }
    },
    data: function() {
        return {
            courses: [],
            coursePlans: [],
            page: 1,
            tooMany: false,
            deleted: false,
            selectedCourse: null,
            new_name: '',
            new_type: '',
            sending: false,
            loading: true
        }
    },
    created: function() {
        this.$http.get(this.api + '?p=' + this.page)
        .then(function(response){
            var data = response.json();

            this.courses = data.data;
            this.tooMany = data.more;

            this.$http.get('http://fhuiguide.com/api/course_plans')
            .then(function(response2){
                var data2 = response2.json();

                this.coursePlans = data2.data;
                this.loading = false;
            });
        });
    },
    methods: {
        createCourse() {
            this.sending = true;
            this.$http.post(this.api, {name: this.new_name, type: this.new_type}, {headers: {Authorization: localStorage.getItem('auth-token')}})
            .then(function(response){
                var data = response.json();

                if (data.status == 'Success') {
                    this.courses.unshift(data.data[0]);

                    this.new_name = '';
                    this.new_type = '';
                } else if (data.status == 'Unauthorized') {
                    alert('Sepertinya Anda belum login. Coba login ulang.');
                } else if (data.status == 'Error') {
                    alert('Terjadi kesalahan! ' + data.message);
                } else {
                    alert('Terjadi kesalahan.');
                }

                this.sending = false;
            });
        },
        deleteCourse: function() {
          this.$http.delete(this.api + '/' + this.selectedCourse, {headers: {Authorization: localStorage.getItem('auth-token')}})
            .then(function(response) {
              var data = response.json();

              if (data.status == 'Success') {
                for (var i = 0; i < this.courses.length; i++) {
                  if (this.courses[i].id == this.selectedCourse) {
                    this.courses[i].id = "deleted";
                    this.deleted = true;
                  }
                }
              } else if (data.status == 'Unauthorized') {
                alert('Sepertinya Anda belum login. Coba login ulang.');
              }
            });
        },
        select: function(id) {
          this.selectedCourse = id;
        },
        removeSelected: function() {
          this.selectedCourse = null;
        },
        loadMore: function(){
            this.page++;
            this.loading = true;

            this.$http.get(this.api + '?p=' + this.page)
            .then(function(response) {
                var data = response.json();

                if (data.status == 'Success' && data.data.length > 0) {
                    this.tooMany = data.more;
                    this.courses = this.courses.concat(data.data);
                } else {
                    console.log(data);
                }
                this.loading = false;
            });
        },
    },
    computed: {
        noCourses: function() {
            return this.courses.length < 1;
        },
        invalid: function() {
            return this.new_name == '' || this.new_type == '';
        }
    }
}

</script>

<style scoped>

#accordion {
    margin-top: 10px;
}

.addnew {
    padding: 10px;
    border: 1px solid #E9ECEF;
}

</style>



