<template>
    <div id="content">
        <div class="container">
            <div class="feature-title">{{title}}</div>

            <div id="items">
                <div v-if="empty && !loading" class="col-12 text-center empty" style="margin-top: 20px;">
                    Saat ini masih belum ada {{ title }}.
                </div>
                <div v-if="loading" class="text-center" style="margin-top: 20px;"><img src="../assets/img/loading.gif" height="30" alt="loading..." /><br />Memuat data...</div>
                <div v-else class="item-list col-12">
                    <table class="table table-sm">
                    <thead>
                        <tr>
                            <th>Mata Kuliah Wajib</th>
                        </tr>
                    </thead>
                    <tbody>
                    <tr v-for="item in items" v-if="item.mandatory == 1">
                        <td v-if="item.design != undefined"><a :href="api + '/..' + item.design" target="_blank"><em>{{ item.name }}</em></a></td>
                        <td v-else><em>{{ item.name }}</em></td>
                    </tr>
                    </tbody>
                    </table>
                    <table class="table table-sm">
                    <thead>
                        <tr>
                            <th>Mata Kuliah Peminatan</th>
                        </tr>
                    </thead>
                    <tbody>
                    <tr v-for="item in items" v-if="item.mandatory == 0">
                        <td v-if="item.design != undefined"><a :href="api + '/..' + item.design" target="_blank"><em>{{ item.name }}</em></a></td>
                        <td v-else><em>{{ item.name }}</em></td>
                    </tr>
                    </tbody>
                    </table>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import TextItem from './TextCenteredItem'
import FullButton from './FullButton'

export default {
    name: 'text-centered-feature',
    data: function() {
        return {
            page: 1,
            items: [],
            loading: true,
            showLoadMore: false
        }
    },
    props: {
        title: {
            default: "Title"
        },
        api: {
            default: "http://localhost/"
        }
    },
    components: {
        TextItem
    },
    computed: {
        empty: function() {
            return this.items.length < 1;
        }
    },
    created: function() {
        this.$http.get(this.api)
        .then(function(response) {
            var data = response.json();
            this.items = data.data;
            this.loading = false;
        });
    }
}
</script>

<style scoped>
.feature-title {
    width: 100%;
    margin-top: 20px;
    font-size: 28px;
    font-family: 'Dosis', sans-serif;
    font-weight: bold;
    color: #FFFFFF;
    border-bottom: 1px solid #FFFFFF;
}

#content {
    margin: 0px;
    background-color: #2D4452;
    padding: 1px 50px 50px 50px;
}

#items {

}

#items .empty {
    color: #FFFFFF;
}

.item-list {
    margin-top: 10px;
    padding-top: 10px;
    background-color: #FFFFFF;
}
</style>



