<template>
<div>
    <div class="row">
        <div class="control col-12">
            <div class="name">Semua Posts</div>

            <table class="table table-bordered table-responsive">
                <thead>
                    <th>Judul Post</th>
                    <th>Tanggal Post</th>
                    <th>Pengaturan</th>
                </thead>
                <tbody>
                    <tr v-if="noPost && !loading" class="text-center">
                        <td colspan="3">Tidak ada post di sini.</td>
                    </tr>
                    <tr v-for="post in posts" v-if="post.id != 'deleted'">
                        <td>{{ post.title }}</td>
                        <td>{{ post.created_at }}</td>
                        <td>
                            <!--button class="btn btn-sm btn-primary">Edit</button-->
                            <button class="btn btn-sm btn-danger" data-toggle="modal" data-target="#deleteModal" v-on:click="select(post.id)">Hapus</button>
                        </td>
                    </tr>
                    <tr v-else>
                        <td colspan="3">Post telah dihapus.</td>
                    </tr>
                    <tr v-if="loading">
                        <td colspan="3" class="text-center">
                            <img src="../assets/img/loading.gif" height="30" alt="loading..." /><br />Memuat data...
                        </td>
                    </tr>
                    <tr v-if="tooMany && !loading">
                        <td colspan="3">
                            <button class="btn btn-secondary btn-block" v-on:click="loadMore()">Lihat lebih banyak...</button>
                        </td>
                    </tr>
                </tbody>
            </table>
        </div>
    </div>
    <div class="row">
        <div class="control col-12">
            <div class="name">Tambah Post Baru</div>
            <form>
                <div v-if="!sending">
                    <div class="form-group">
                        <input type="text" class="form-control input-sm" placeholder="Judul" v-model="new_post_title" required>
                    </div>
                    <div class="form-group">
                        <editor name="editor" :model="new_post_content" v-on:change="value => { new_post_content = value }"></editor>
                    </div>
                    <button type="submit" class="btn btn-secondary btn-block" v-on:click="createPost($event)" :disabled="new_post_title == '' || strippedContent == ''">Post</button>
                </div>
                <div class="col-12 text-center" v-else><img src="../assets/img/loading.gif" height="30" alt="loading..." /><br />Posting...</div>
            </form>
        </div>
    </div>

    <div class="modal fade" id="deleteModal" tabindex="-1" role="dialog" aria-labelledby="deleteModal" aria-hidden="true">
        <div class="modal-dialog" role="document">
            <div class="modal-content">
                <div class="modal-header">
                    <h5 class="modal-title">Hapus Post</h5>
                    <button type="button" class="close" data-dismiss="modal" aria-label="Close" v-on:click="removeSelected()">
                        <span aria-hidden="true">&times;</span>
                    </button>
                </div>
                <div class="modal-body">
                    <p>Apakah kamu yakin akan menghapus post tersebut?</p>
                </div>
                <div class="modal-footer">
                    <button type="button" class="btn btn-danger" data-dismiss="modal" v-on:click="deletePost()">Hapus</button>
                    <button type="button" class="btn btn-secondary" data-dismiss="modal" v-on:click="removeSelected()">Batal</button>
                </div>
            </div>
        </div>
    </div>
</div>
</template>

<script>
var striptags = require('striptags');

export default {
    name: 'text-centered-control',
    data: function() {
        return {
            posts: [],
            page : 1,
            tooMany: false,
            selectedPost: null,
            new_post_title: '',
            new_post_content: '',
            sending: false,
            loading: true
        }
    },
    props: {
        api: {
            default: "http://localhost/"
        }
    },
    methods: {
        createPost: function(e) {
            e.preventDefault();

            this.sending = true;

            this.$http.post(this.api, {title: this.new_post_title, content: this.new_post_content}, {headers: {Authorization: localStorage.getItem('auth-token')}})
            .then(function(response) {
                var data = response.json();

                if (data.status == 'Success') {
                    this.posts.unshift(data.data[0]);

                    this.new_post_title = "";
                    this.new_post_content = "";
                } else if (data.status == 'Unauthorized') {
                    alert('Sepertinya Anda belum login. Coba login ulang.');
                } else {
                    alert('Terjadi kesalahan.');
                }

                this.sending = false;
            }).catch(function(error){
                alert("Terjadi kesalahan! Mungkin ukuran file terlalu besar?");
                this.sending = false;
            });
        },
        deletePost: function() {
            this.$http.delete(this.api + '/' + this.selectedPost, {headers: {Authorization: localStorage.getItem('auth-token')}})
            .then(function(response) {
                var data = response.json();

                if (data.status == 'Success') {
                    for (var i = 0; i < this.posts.length; i++) {
                        if (this.posts[i].id == this.selectedPost) {
                            this.posts[i].id = "deleted";
                        }
                    }
                } else if (data.status == 'Unauthorized') {
                    alert('Sepertinya Anda belum login. Coba login ulang.');
                }
            });
        },
        loadMore: function(){
            this.page++;
            this.loading = true;

            this.$http.get(this.api + '?p=' + this.page)
            .then(function(response) {
                var data = response.json();

                if (data.status == 'Success' && data.data.length > 0) {
                    this.tooMany = data.more;
                    this.posts = this.posts.concat(data.data);
                } else {
                    console.log(data);
                }
                this.loading = false;
            });
        },
        select: function(id) {
            this.selectedPost = id;
        },
        removeSelected: function() {
            this.selectedPost = null;
        }
    },
    created: function() {
        this.$http.get(this.api)
        .then(function(response){
            var data = response.json();

            this.posts = data.data;
            this.tooMany = data.more;
            this.loading = false;
        });
    },
    computed: {
        noPost: function() {
            return this.posts.length < 1;
        },
        strippedContent: function() {
            return striptags(this.new_post_content, ['img']);
        }
    },
    components: {
        "editor": require("./Summernote")
    }
}

</script>

<style scoped>
.control {
    margin-top: 10px;
    padding: 10px;
    background-color: #FFFFFF;
}

.control .name {
    border-bottom: 1px solid #E9ECEF;
    margin-bottom: 5px;
}

.control form {
    padding: 10px;
    border: 1px solid #E9ECEF;
}
</style>



