/* harmony default export */ __webpack_exports__["a"] = ({
    name: "matkul-manager",
    props: {
        api: {
            default: "http://fhuiguide.com/api"
        }
    },
    data: function data() {
        return {
            courses: [],
            coursePlans: [],
            page: 1,
            tooMany: false,
            deleted: false,
            selectedCourse: null,
            new_name: '',
            new_type: '',
            sending: false,
            loading: true
        };
    },
    created: function created() {
        this.$http.get(this.api + '?p=' + this.page).then(function (response) {
            var data = response.json();

            this.courses = data.data;
            this.tooMany = data.more;

            this.$http.get('http://fhuiguide.com/api/course_plans').then(function (response2) {
                var data2 = response2.json();

                this.coursePlans = data2.data;
                this.loading = false;
            });
        });
    },
    methods: {
        createCourse: function createCourse() {
            this.sending = true;
            this.$http.post(this.api, { name: this.new_name, type: this.new_type }, { headers: { Authorization: localStorage.getItem('auth-token') } }).then(function (response) {
                var data = response.json();

                if (data.status == 'Success') {
                    this.courses.unshift(data.data[0]);

                    this.new_name = '';
                    this.new_type = '';
                } else if (data.status == 'Unauthorized') {
                    alert('Sepertinya Anda belum login. Coba login ulang.');
                } else if (data.status == 'Error') {
                    alert('Terjadi kesalahan! ' + data.message);
                } else {
                    alert('Terjadi kesalahan.');
                }

                this.sending = false;
            });
        },

        deleteCourse: function deleteCourse() {
            this.$http.delete(this.api + '/' + this.selectedCourse, { headers: { Authorization: localStorage.getItem('auth-token') } }).then(function (response) {
                var data = response.json();

                if (data.status == 'Success') {
                    for (var i = 0; i < this.courses.length; i++) {
                        if (this.courses[i].id == this.selectedCourse) {
                            this.courses[i].id = "deleted";
                            this.deleted = true;
                        }
                    }
                } else if (data.status == 'Unauthorized') {
                    alert('Sepertinya Anda belum login. Coba login ulang.');
                }
            });
        },
        select: function select(id) {
            this.selectedCourse = id;
        },
        removeSelected: function removeSelected() {
            this.selectedCourse = null;
        },
        loadMore: function loadMore() {
            this.page++;
            this.loading = true;

            this.$http.get(this.api + '?p=' + this.page).then(function (response) {
                var data = response.json();

                if (data.status == 'Success' && data.data.length > 0) {
                    this.tooMany = data.more;
                    this.courses = this.courses.concat(data.data);
                } else {
                    console.log(data);
                }
                this.loading = false;
            });
        }
    },
    computed: {
        noCourses: function noCourses() {
            return this.courses.length < 1;
        },
        invalid: function invalid() {
            return this.new_name == '' || this.new_type == '';
        }
    }
});

